<template>
  <div class="main-layout">
    <!-- 사이드바 -->
    <div class="sidebar" :class="{ collapsed: sidebarCollapsed }">
      <div class="sidebar-header">
        <div class="logo">
          <el-icon class="logo-icon"><OfficeBuilding /></el-icon>
          <span v-if="!sidebarCollapsed" class="logo-text">H.ONE</span>
        </div>
        <el-button 
          type="text" 
          @click="toggleSidebar"
          class="collapse-btn"
        >
          <el-icon><Expand v-if="sidebarCollapsed" /><Fold v-else /></el-icon>
        </el-button>
      </div>
      
      <el-menu
        :default-active="$route.path"
        :collapse="sidebarCollapsed"
        router
        class="sidebar-menu"
      >
        <el-menu-item index="/">
          <el-icon><House /></el-icon>
          <span>대시보드</span>
        </el-menu-item>
        
        <el-sub-menu index="hr">
          <template #title>
            <el-icon><User /></el-icon>
            <span>HR 관리</span>
          </template>
          <el-menu-item index="/organization">조직도</el-menu-item>
          <el-menu-item index="/employee">직원 관리</el-menu-item>
          <el-menu-item index="/attendance">근태 관리</el-menu-item>
          <el-menu-item index="/payroll">급여 관리</el-menu-item>
        </el-sub-menu>
        
        <el-sub-menu index="collaboration">
          <template #title>
            <el-icon><ChatDotRound /></el-icon>
            <span>협업</span>
          </template>
          <el-menu-item index="/chat">채팅</el-menu-item>
          <el-menu-item index="/meeting">화상회의</el-menu-item>
          <el-menu-item index="/approval">전자결재</el-menu-item>
          <el-menu-item index="/board">게시판</el-menu-item>
        </el-sub-menu>
        
        <el-menu-item index="/resource">
          <el-icon><Calendar /></el-icon>
          <span>자원 예약</span>
        </el-menu-item>
      </el-menu>
    </div>

    <!-- 메인 컨텐츠 영역 -->
    <div class="main-content">
      <!-- 상단 헤더 -->
      <div class="header">
        <div class="header-left">
          <h1 class="page-title">{{ getPageTitle() }}</h1>
        </div>
        
        <div class="header-right">
          <!-- 알림 -->
          <el-popover
            placement="bottom-end"
            :width="320"
            trigger="click"
          >
            <template #reference>
              <el-badge :value="notifications.length" class="notification-badge">
                <el-button type="text" class="notification-btn">
                  <el-icon><Bell /></el-icon>
                </el-button>
              </el-badge>
            </template>
            
            <div class="notification-panel">
              <div class="notification-header">
                <h3>알림</h3>
                <el-button type="text" size="small">모두 읽음</el-button>
              </div>
              <div class="notification-list">
                <div 
                  v-for="notification in notifications" 
                  :key="notification.id"
                  class="notification-item"
                >
                  <div class="notification-content">
                    <div class="notification-title">{{ notification.title }}</div>
                    <div class="notification-message">{{ notification.message }}</div>
                    <div class="notification-time">{{ notification.time }}</div>
                  </div>
                  <el-button 
                    type="text" 
                    size="small"
                    @click="removeNotification(notification.id)"
                  >
                    <el-icon><Close /></el-icon>
                  </el-button>
                </div>
              </div>
            </div>
          </el-popover>
          
          <!-- 사용자 메뉴 -->
          <el-dropdown @command="handleUserCommand">
            <div class="user-profile">
              <el-avatar :src="user.avatar" :size="32" />
              <span class="user-name">{{ user.name }}</span>
              <el-icon><ArrowDown /></el-icon>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item command="profile">프로필</el-dropdown-item>
                <el-dropdown-item command="settings">설정</el-dropdown-item>
                <el-dropdown-item command="logout" divided>로그아웃</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </div>

      <!-- 페이지 컨텐츠 -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'MainLayout',
  data() {
    return {
      sidebarCollapsed: false
    }
  },
  computed: {
    ...mapState(['user', 'notifications'])
  },
  methods: {
    ...mapMutations(['removeNotification']),
    toggleSidebar() {
      this.sidebarCollapsed = !this.sidebarCollapsed
    },
    getPageTitle() {
      const titles = {
        '/': '대시보드',
        '/organization': '조직도',
        '/employee': '직원 관리',
        '/attendance': '근태 관리',
        '/payroll': '급여 관리',
        '/chat': '채팅',
        '/meeting': '화상회의',
        '/approval': '전자결재',
        '/board': '게시판',
        '/resource': '자원 예약'
      }
      return titles[this.$route.path] || 'H.ONE'
    },
    handleUserCommand(command) {
      switch (command) {
        case 'profile':
          this.$message.info('프로필 페이지로 이동')
          break
        case 'settings':
          this.$message.info('설정 페이지로 이동')
          break
        case 'logout':
          this.$message.info('로그아웃')
          break
      }
    }
  }
}
</script>

<style scoped>
.main-layout {
  display: flex;
  height: 100vh;
  background-color: #f5f7fa;
}

.sidebar {
  width: 240px;
  background: #4f46e5;
  color: white;
  transition: width 0.3s ease;
  overflow: hidden;
}

.sidebar.collapsed {
  width: 64px;
}

.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-icon {
  font-size: 24px;
  color: #fff;
}

.logo-text {
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.collapse-btn {
  color: white !important;
  font-size: 16px;
}

.sidebar-menu {
  border: none;
  background: transparent;
}

.sidebar-menu .el-menu-item,
.sidebar-menu .el-sub-menu__title {
  color: rgba(255, 255, 255, 0.8);
  background: transparent;
}

.sidebar-menu .el-menu-item:hover,
.sidebar-menu .el-sub-menu__title:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.sidebar-menu .el-menu-item.is-active {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.header {
  background: white;
  padding: 0 24px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.page-title {
  font-size: 24px;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.notification-badge {
  cursor: pointer;
}

.notification-btn {
  font-size: 18px;
  color: #606266;
}

.notification-panel {
  padding: 16px;
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid #ebeef5;
}

.notification-header h3 {
  margin: 0;
  font-size: 16px;
  color: #303133;
}

.notification-list {
  max-height: 300px;
  overflow-y: auto;
}

.notification-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 12px 0;
  border-bottom: 1px solid #f0f0f0;
}

.notification-item:last-child {
  border-bottom: none;
}

.notification-content {
  flex: 1;
}

.notification-title {
  font-weight: 500;
  color: #303133;
  margin-bottom: 4px;
}

.notification-message {
  font-size: 14px;
  color: #606266;
  margin-bottom: 4px;
}

.notification-time {
  font-size: 12px;
  color: #909399;
}

.user-profile {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 6px;
  transition: background-color 0.3s;
}

.user-profile:hover {
  background-color: #f5f7fa;
}

.user-name {
  font-weight: 500;
  color: #303133;
}

.content {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
}
</style>
